@import url(https://fonts.googleapis.com/css2?family=Goldman&display=swap);
.navbar {
  list-style: none;
  display: flex;
  border-bottom: 1px solid white;
  width: 85vw;
  margin: auto;
  font-family: "Goldman", cursive;
  padding-left: 120px;
}
.navbar li {
  cursor: pointer;
  padding: 0 30px;
  height: 100px;
  font-size: 20px;
  color: white;
  line-height: 100px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.navbar li:hover {
  border-bottom: 5px solid rgb(56, 94, 217);
}
.loginBt {
  position: relative;
  left: 320px;
  text-decoration: none;
}
.logo {
  width: 80px;
  height: 60px;
  position: absolute;
  top: 20px;
  left: 140px;
  cursor: pointer;
}

.screen {
  z-index: -1000;
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: center/cover no-repeat
    url("https://www.bmw.co.kr/content/dam/bmw/marketKR/bmw_co_kr/all-models/5-series/THE-5-KV_PC2.jpg/_jcr_content/renditions/cq5dam.resized.img.1680.large.time1599648810252.jpg");
}

* {
  margin: 0;
  box-sizing: border-box;
}

